*{
  margin: 0px 0px;
  box-sizing: border-box;
  font-family: 'DM Sans', sans-serif;
}
body{
  padding-left: 5vw;
  padding-right: 5vw;
  padding-top: 8vh;
  overflow-x: hidden;
  background-color: rgb(231, 231, 231);
}
html{
  scroll-behavior: smooth;
}
.orange-art{
  height: 600px;
  width: 600px;
  background-color: orange;
  border-radius: 50%;
  position: absolute;
  z-index: -1;
  top: -100px;
  left: -100px;
}
main{
  display: flex;
  box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}
img{
  border-radius: 50%;
  width: 70px;
  height: 70px;
}
.logo_title{
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 100px;
}
.leftpanel{
  width: 50%;
  padding: 8vw;
  background-color: white;
}
.leftpanel h1{
  font-size: 45px;
}
.leftpanel h2{
  font-size: 35px;
  margin-bottom: 40px;
}
.leftpanel h4{
  font-weight: 400;
}
form{
  width: 50%;
  padding: 8vw;
  background-color: white;
}
.inp-error{
  width: 100%;
  border: 1px solid red;
  border-style: dashed;
  background-color: rgb(255, 163, 163);
  color: white;
  padding: 5px;
  padding-left: 10px;
  display: none;
}
.feedback_type{
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.form_elem{
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
input{
  height: 30px;
  padding-left: 10px;
  border: 1px solid black;
  border-radius: 5px;
}
input:focus, textarea {
  outline: none !important;
}
#type1, #type2{
  height: 15px;
  margin-right: 10px;
  margin-left: 20px;
}
select, textarea{
  height: 30px;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  width: 100%;
  border: 0;
  font-family: inherit;
  padding: 16px 12px 0 12px;
  height: 56px;
  font-size: 16px;
  font-weight: 400;
  background: rgba(0, 0, 0, 0.02);
  box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.3);
  color: #949494;
}
select:has(option[value=""]:not(:checked)){
  color: rgba(0, 0, 0, 0.5);
}
#submit_btn{
  cursor: pointer;
  display: inline-block;
  margin-top: 40px;
  margin-bottom: 40px;
  text-align: center;
  background-color: blue;
  color: white;
  text-decoration: none;
  height: 40px;
  width: 80px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
}

:root {
  --primary: #0077FF;
  --dark: #000;
}

.inp {
  position: relative;
  margin: auto;
  width: 100%;
  border-radius: 3px;
  overflow: hidden;
}
.inp .label {
  position: absolute;
  top: 20px;
  left: 12px;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.5);
  font-weight: 500;
  transform-origin: 0 0;
  transform: translate3d(0, 0, 0);
  transition: all 0.2s ease;
  pointer-events: none;
}
.inp .focus-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.05);
  z-index: -1;
  transform: scaleX(0);
  transform-origin: left;
}
.inp input {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  width: 100%;
  border: 0;
  font-family: inherit;
  padding: 16px 12px 0 12px;
  height: 56px;
  font-size: 16px;
  font-weight: 400;
  background: rgba(0, 0, 0, 0.02);
  box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.3);
  color: #000;
  transition: all 0.15s ease;
}
.inp input:hover {
  background: rgba(0, 0, 0, 0.04);
  box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.5);
}
.inp input:not(:-moz-placeholder-shown) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp input:not(:-ms-input-placeholder) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp input:not(:placeholder-shown) + .label {
  color: rgba(0, 0, 0, 0.5);
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp input:focus {
  background: rgba(0, 0, 0, 0.05);
  outline: none;
  box-shadow: inset 0 -2px 0 #0077FF;
}
.inp input:focus + .label {
  color: #0077FF;
  transform: translate3d(0, -12px, 0) scale(0.75);
}
.inp input:focus + .label + .focus-bg {
  transform: scaleX(1);
  transition: all 0.1s ease;
}
textarea:focus{
  background-color: rgb(234, 234, 234);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
@media screen and (max-width: 950px) {
  main{
    flex-direction: column;
  }
  form, .leftpanel{
    width: 100%;
  }
}
@media screen and (max-width: 618px) {
  .logo_title{
    max-width: 80%;
  }
  .logo_title img{
    max-width: 20vw;
    max-height: 20vw;
  }
  .logo_title h1{
    font-size: 9vw;
  }
  main{
    min-width: 320px;
  }
}


.body-box{
  width: 100%;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-left: 5vw;
  padding-right: 5vw;
}
.title-feedback *, .response-table h3{
  text-align: center;
}
.title-feedback{
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.response-table{
  margin-top: 40px;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 70%;
}
table{
  border-collapse: collapse;
  min-width: 90%;
}
tr{
  height: 50px;
  transition: scale 0.2s;
}
td{
  padding-left: 10px;
  padding-right: 10px;
  background-color: rgb(235, 235, 235);
  white-space:pre-wrap;
  word-wrap:break-word
}
.thead{
  background-color: rgb(55, 55, 191);
  width: 190px;
  color: white;
}
tr:hover{
  scale: 1.02;
}